.head {
  text-align: center;
}

.text {
  color: #f77062;
}

/* Products-Page */
.site-title {
  text-align: center;
  font-weight: normal;
  font-size: 2rem;
}
.prod {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.products {
  text-align: center;
  border: solid 3px #f77062;
  width: 550px;
  padding: 10px 15px;
  margin-top: 15px;
  margin-bottom: 10px;
}
.img {
  width: 55%;
  height: auto;
}

.device {
  font-weight: bold;
  margin: 20px;
}

.price {
  margin-top: 15px;
}

/* PRODUCTS-HOME */
.product {
  position: relative;
  overflow: hidden;
  padding: 15px;
}

.product-brand {
  color: #131313;
}

.product-category {
  text-align: center;
  padding: 0 10vw;
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 40px;
  text-transform: capitalize;
}

.product-container {
  padding: 0 10vw;
  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
}

.product-card {
  flex: 0 0 auto;
  width: 250px;
  height: 525px;
  margin-right: 40px;
}

.product-image {
  position: relative;
  width: 100%;
  height: 350px;
  overflow: hidden;
}

.product-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-btn {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px;
  width: 90%;
  text-transform: capitalize;
  border: none;
  outline: none;
  background: #fff;
  border-radius: 5px;
  transition: 0.5s;
  cursor: pointer;
  opacity: 0;
}

.product-card:hover .card-btn {
  opacity: 1;
}

.card-btn:hover {
  background: #f77062;
  color: #fff;
}

.product-info {
  width: 100%;
  height: 100px;
  padding-top: 10px;
}

.price {
  font-weight: 900;
  font-size: 20px;
}

.actual-price {
  margin-left: 20px;
  opacity: 0.5;
  text-decoration: line-through;
}

.pre-btn,
.nxt-btn {
  border: none;
  width: 10vw;
  height: 100%;
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #fff 100%);
  cursor: pointer;
  z-index: 8;
}

.pre-btn {
  left: 0;
  transform: rotate(180deg);
}

.nxt-btn {
  right: 0;
}

.pre-btn img,
.nxt-btn img {
  opacity: 0.2;
}

.pre-btn:hover img,
.nxt-btn:hover img {
  opacity: 1;
}
