* {
  margin: 0;
  padding: 0;
  font-family: "Kumbh Sans", sans-serif;
}

/* Contact Page Styling */
.wrapper {
  margin: 0px auto 0;
  width: 100%;
  max-width: 680px;
  padding: 20px;
  box-sizing: border-box;
}

/* content */
.content {
  text-align: center;
}

.content h1 {
  letter-spacing: 1px;
}

/* form */
.form {
  width: 100%;
  margin: 25px 0;
}

.top-form,
.middle-form,
.bottom-form {
  width: 100%;
  min-height: 65px;
  margin: 10px 0;
}

.form input[type="text"],
.form textarea {
  border: 2px solid #fff;
  padding: 10px 5px;
  outline: none;
  border-radius: 2px;
  width: 100%;
  transition: all 0.2s ease;
}

.form input:focus,
.form textarea:focus {
  border-color: #f77062;
  outline: none;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.0125),
    0 0 8px rgba(76, 161, 175, 0.5);
}

.form label {
  margin-bottom: 5px;
}

/* top-form */
.top-form .inner-form {
  margin-right: 15px;
  width: 30%;
  float: left;
  /* margin-right: 5%; */
}

.top-form .inner-form:last-child {
  margin-right: 0;
}

/* middle-form */
.middle-form {
  clear: both;
}

/* bottom-form */
.bottom-form textarea {
  height: 120px;
}

.btn {
  background: #f77062;
  width: 200px;
  padding: 10px 0;
  border-radius: 4px;
  text-align: center;
  color: #fff;
  letter-spacing: 5px;
  cursor: pointer;
}

.btn:hover {
  background: #4837ff;
  transition: all 0.3s ease;
}

/* Mobile Responsiveness */
@media screen and (max-width: 480px) {
  .wrapper {
    margin: 25px auto 0;
  }

  .top-form .inner-form {
    width: 100%;
    margin: 5px 0;
  }
}
